import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Aside from "./components/Aside";
import Box from "./components/Box";
import Buttons from "./components/Buttons";
import Collapse from "./components/Collapse";
import Dropdown from "./components/Dropdown";
import Form from "./components/Form";
import Grid from "./components/Grid";
import Header from "./components/Header";
import HeaderNav from "./components/HeaderNav";
import Home from "./components/Home";
import Install from "./components/Install";
import Modal from "./components/Modal";
import Navigation from "./components/Navigation";
import Sidebar from "./components/Sidebar";
import Table from "./components/Table";
import Tab from "./components/Tab";
import Themes from "./components/Themes";
import Typography from "./components/Typography";
import Footer from "./components/Footer";
import FooterNav from "./components/FooterNav";
import LinkPage from "./components/LinkPage";

// import "./scss/bsix.scss";
// import "./scss/theme/bsix-themes.scss";

// import "./cssmin/bsix.css";
// import "./cssmin/theme/bsix-themes.css";

import "./scss/prism.scss";

function App() {
  return (
    <Router>
      <Aside />
      <div className="c10 m2">
        <Header />
        <div className="container">
          <Route exact path={process.env.PUBLIC_URL +"/"} component={Home} />
          <Route exact path={process.env.PUBLIC_URL +"/box"} component={Box} />
          <Route exact path={process.env.PUBLIC_URL +"/buttons"} component={Buttons} />
          <Route exact path={process.env.PUBLIC_URL +"/collapse"} component={Collapse} />
          <Route exact path={process.env.PUBLIC_URL +"/dropdown"} component={Dropdown} />
          <Route exact path={process.env.PUBLIC_URL +"/form"} component={Form} />
          <Route exact path={process.env.PUBLIC_URL +"/footer"} component={FooterNav} />
          <Route exact path={process.env.PUBLIC_URL +"/grid"} component={Grid} />
          <Route exact path={process.env.PUBLIC_URL +"/headernav"} component={HeaderNav} />
          <Route exact path={process.env.PUBLIC_URL +"/scss"} component={Home} />
          <Route exact path={process.env.PUBLIC_URL +"/home"} component={Home} />
          <Route exact path={process.env.PUBLIC_URL +"/install"} component={Install} />
          <Route exact path={process.env.PUBLIC_URL +"/linkpage"} component={LinkPage} />
          <Route exact path={process.env.PUBLIC_URL +"/modal"} component={Modal} />
          <Route exact path={process.env.PUBLIC_URL +"/navigation"} component={Navigation} />
          <Route exact path={process.env.PUBLIC_URL +"/sidebar"} component={Sidebar} />
          <Route exact path={process.env.PUBLIC_URL +"/tab"} component={Tab} />
          <Route exact path={process.env.PUBLIC_URL +"/table"} component={Table} />
          <Route exact path={process.env.PUBLIC_URL +"/themes"} component={Themes} />
          <Route exact path={process.env.PUBLIC_URL +"/typography"} component={Typography} />
        </div>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
